import { Component , OnInit , ViewChild } from '@angular/core';
import {AlertController, ModalController ,  IonRouterOutlet} from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import {  Router, NavigationExtras } from '@angular/router';
import { AuthenticationService } from '../../services/Authentication.service';
import { FormGroup, FormControl } from '@angular/forms';
import { ReplaySubject} from 'rxjs';
import { IonInfiniteScroll } from '@ionic/angular';
import { FormsCreatePage } from './forms-create/forms-create.page';
@Component({
selector: 'page-forms-list',
templateUrl: 'forms-list.page.html',
styleUrls: ['./forms-list.page.scss'],
})
export class FormsListPage implements OnInit {
@ViewChild(IonInfiniteScroll, { static : false }) infiniteScroll: IonInfiniteScroll;
rangeForm: FormGroup;
searchQuery: string;
showAgeFilter = true; showmenu = false;
items: any  = [] ;
variables: any = [];
userinfo: any = [];
categoria:any;
tipo:any;
idllave: any;
viewType = 'grid';
tituloxx = '';
efecto = 0;
seguro = '';
msg = '';
apifoto: String;
itemsList: Array<any>;
public currentSegment = 'Todos';  
public currentSegment2 ;
searchSubject: ReplaySubject<any> = new ReplaySubject<any>(1);
constructor(  private routerOutlet: IonRouterOutlet , private router: Router, private authenticationService: AuthenticationService,
   public alertController: AlertController,     public translateService: TranslateService    ,    private modalController: ModalController
   ) {  this.apifoto = this.authenticationService.getapifoto();
 }
changeToList() { this.viewType = 'list'; }
changeToGrid() {   this.viewType = 'grid';  }
ngOnInit() {
    this.variables = this.router.getCurrentNavigation().extras.state.variables;
    this.idllave = this.router.getCurrentNavigation().extras.state.idllave;
    this.userinfo = this.authenticationService.getcopiauser();
    this.tituloxx =this.router.getCurrentNavigation().extras.state.title;
    this.categoria =this.router.getCurrentNavigation().extras.state.categoria;
    this.currentSegment2=  this.router.getCurrentNavigation().extras.state.tipo;
    this.searchQuery = '';
    this.translateService.get('SEGUROM').subscribe((value) => {   this.seguro = value;  });
    this.translateService.get('MSGM').subscribe((value) => {   this.msg = value;  });
    this.rangeForm = new FormGroup({    dual: new FormControl({lower: 1, upper: 100})   });
    this.currentSegment = 'todos';
  
    this.pideregistros( this.currentSegment, this.currentSegment2);
  }
  filterContacts2(event: any) {
    const selectedCategory2 = event.detail.value;
    this.currentSegment2 = selectedCategory2;
    this.pideregistros(this.currentSegment, this.currentSegment2 );
  }
  filterContacts(event: any) {
    const selectedCategory = event.detail.value;
    this.currentSegment = selectedCategory;
    this.pideregistros(selectedCategory, this.currentSegment2);
  }
  pideregistros0() {
   this.pideregistros(this.currentSegment, this.currentSegment2);
  }
  pideregistros(op, op2) {
       this.itemsList = [];
       this.efecto = 0 ;
      // this.authenticationService.getReg('todos/' + op + '/' + this.userinfo[0].idempresa + '/' + this.idllave, 'formss', 0).then((data) => {
       this.authenticationService.getReg(op2 + '/' + op + '/' + this.userinfo[0].idempresa._id + '/' + 'frmmovilp/' +  this.categoria, 'frmmovils', 0).then((data) => {
       this.efecto = 1;
       this.items =  data;
       this.itemsList = this.items;
 }, (err) => {
   this.authenticationService.presentAlert(err.error , this.msg , '');
 });
  }
  searchList() {
    this.searchSubject.next({
     lower: this.rangeForm.controls.dual.value.lower,
     upper: this.rangeForm.controls.dual.value.upper,
     query: this.searchQuery
   });
    const query = (this.searchQuery && this.searchQuery !== null) ? this.searchQuery : '';
    this.itemsList = this.filterList(this.items, query);
  }
 filterList(list, query): Array<any> {
   return list.filter(item => item.nombre.toLowerCase().includes(query.toLowerCase()));
  }
  async otrasop(item: any) {
    let opciones: any = [];


    if (this.currentSegment2 === 'proceso') {
      opciones.push( {
        name: 'campos',        type: 'radio',        label: 'Campos del ' + this.currentSegment2,        checked: true,        value: '/frmmovildListPage°Campos ->' + item.nombre,
       });
       opciones.push( {
        name: 'campos',        type: 'radio',        label: 'Actores del ' + this.currentSegment2,              value: '/frmactorListPage°Actores ->' + item.nombre,
       });
       opciones.push( {
        name: 'campos',        type: 'radio',        label: 'Actividades del ' + this.currentSegment2,            value: '/frmactividadListPage°Actividades ->' + item.nombre,
       });

       


    } else {
      opciones.push( {
        name: 'campos',        type: 'radio',        label: 'Campos del ' + this.currentSegment2,        checked: true,        value: '/frmmovildListPage°Campos ->' + item.nombre,
       });


    }


      const alert = await this.alertController.create({
        cssClass: 'my-custom-class',
       header: 'Seleccione Opción',
       inputs: opciones,
       buttons: [
         {
           text: 'Cancel',
           role: 'cancel',
           cssClass: 'secondary',
           handler: (todo) => {
             console.log(todo);
           }
         }, {
           text: 'Ok',
           handler: (todo) => {
             const arr = todo.split('°');
             const navigationExtras: NavigationExtras = {
               state: {
                 variables: this.variables,
                 user: null,
                 idllave: item._id,
                 title: arr[1]
               }
             };
             this.router.navigate([arr[0]], navigationExtras);
           }
         }
       ]
     });
     await alert.present();
 }
   async deleteRecord(item: any) {
    const alert = await this.alertController.create({
      header: this.seguro,
      message: '',
      buttons: [
        {
          text: 'No',
         cssClass: 'secondary',
          handler: (blah) => {
          }
        }, {
           text: 'Si',
           handler: () => {
             const recordID: string		= item._id;
             this.authenticationService.deleteReg(recordID + '/' + this.userinfo[0].email + '/' + this.userinfo[0].idempresa._id  + '/'
             + 'na' , 'frmmovils').then((data) => {
               this.pideregistros( this.currentSegment, this.currentSegment2);
               }, (err) => {
                 this.authenticationService.presentAlert(err.error, this.msg, '');
             });
           }
         }
       ]
     });
     await alert.present();
   }
  async updateRecord(item: any) {
    const modal = await this.modalController.create({ 
      component: FormsCreatePage,
      swipeToClose: true,
      presentingElement: this.routerOutlet.nativeEl,
      componentProps:  { variables:  this.variables, tipofrm: this.currentSegment2, record: item, userinfo: this.userinfo, idllave: this.idllave, title: this.tituloxx + ' -> ' + item.nombre}
      , cssClass: 'modal-fullscreen'
    });
    modal.onWillDismiss().then((data) => {
         if ( data.data !== 'close' ) {
      this.pideregistros( this.currentSegment, this.currentSegment2);
      }
    });
    return await modal.present();
}
   async  addRecord() {
    const modal = await this.modalController.create({
      component: FormsCreatePage,
      swipeToClose: false,
      backdropDismiss: false,
     animated: true,
      presentingElement: await this.modalController.getTop(),
      componentProps:  { variables:  this.variables, record: null, tipofrm: this.currentSegment2, userinfo: this.userinfo, idllave: this.idllave, title: this.tituloxx}
      , cssClass: 'modal-fullscreen'
    });
    modal.onWillDismiss().then((data) => {
         if ( data.data !== 'close' ) {
        this.pideregistros( this.currentSegment, this.currentSegment2);
      }
    });
    return await modal.present();
   }
   loadData(event) {
    setTimeout(() => {
      event.target.complete();
      if ( this.items.length === 1000) {
        event.target.disabled = true;
      }
    }, 500);
  }


  async  porcategoria1(arr) {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
     header: 'Seleccione Opción',
     inputs: arr,
     buttons: [
       {
         text: 'Cancel',
         role: 'cancel',
         cssClass: 'secondary',
         handler: (todo) => {
           console.log(todo);
         }
       }, {
         text: 'Ok',
         handler: (todo) => {
          this.itemsList = this.items;
          this.itemsList = this.items.filter((v) => {
            if (v.categoria ) {

              if (v.categoria === todo) {
                return true;
              }
              return false;

            }
          });
         }
       }
     ]
   });
   await alert.present();
  }
 async  porcategoria() {

  this.authenticationService.getReg(this.userinfo[0].idempresa._id + '/categoriagrupo/' + this.currentSegment2, 'frmmovils', 0).then((data) => {
    let aa: any;
    aa = data;
    const select: any = [];
    for (let i = 0; i < aa.length; i++) {
      select.push({name: 'myInput', type: 'radio', label: aa[i].categoria, value: aa[i].categoria});
  }
  this.porcategoria1(select);

}, (err) => {
  this.authenticationService.presentAlert(err.error , this.msg , '');
});
   }

}
